<template>
  <div id="login">
      <div class="login-list">
            <div class="login-title">
                <h3>账号登录</h3>
            </div>
          <input type="text" class="input-text" placeholder="账号" v-model="inputName">
          <input type="text" class="input-text" placeholder="密码" v-model="inputPassword">
          <input type="button" class="btn" value="登录" @click="login()">
      </div>
  </div>
</template>

<script>
import * as utils from './../utils/index'
export default {
    name: 'login',
    data () {
        return {
            inputName: '',
            inputPassword: ''
        }
    },
    methods: {
        pushRouter(router){
            this.$router.push(router)
        },
        login(){
            if(this.inputName == 'zhiyu' && this.inputPassword == '123'){
                this.$store.commit('setUserName', this.inputName)
                this.$store.commit('setShowLog', false)
                utils.setLocalStorage('userName', this.inputName)
                this.pushRouter('/')
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    .login-list
        margin 100px auto 200px
        width 260px
        .login-title
            color #aaa
            text-align center
        .input-text
            display block
            margin 20px 0
            width 260px
            height 30px
            outline none
        .btn
            width 100%
            height 30px
            background #cccccc
            border none
            color #aaa
            outline none
            cursor pointer
</style>




