<template>
  <div id="goods-list">
      <div class="good-list-itme" v-for="(itme, index) of goodsData" :key="index" @click="pushRouter('/goods/living-room/'+itme.goodsName)">
          <img :src="itme.imgUrl" alt="">
          <div class="itme-info">
            <div class="goods-name">{{itme.goodsName}}</div>
            <div class="goods-price">￥{{itme.goodsPrice}}</div>
          </div>
      </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    name: 'goods',
    data () {
        return {
            goodsListName: '',
            goodsData: []
        }
    },
    computed: {
      ...mapGetters[
          'getRoutePath'
      ]  
    },
    created () {
        this.initGoodsName(this.$route.path.slice(7))
        this.getGoodsData()
    },
    watch: {
      '$route': function(route) {
          this.initGoodsName(route.params.name)
          this.getGoodsData()
      }
    },
    methods: {
        pushRouter(push){
            this.$router.push(push)
            scrollTo(0,0)
        },
        initGoodsName(name){
            switch(name){
                case 'living-room':
                    this.goodsListName = 'liveingRoom'
                    break
                case 'bedr-room':
                    this.goodsListName = 'bedrRoom'
                    break
            }
        },
        getGoodsData(){
            this.$http.get('./../../../../static/data/goodsData.json')
                .then(res => {
                    this.goodsData = res.data[this.goodsListName]['goodsData']
                })
        }
    }
}
</script>

<style lang="stylus" scoped>
    #goods-list
        display flex
        flex-wrap wrap
        width 90%
        margin 50px auto
        .good-list-itme
            width 24%
            height 330px
            margin-top 20px
            margin-left 0.5%            
            margin-right 0.5%
            box-sizing border-box
            border 1px solid #ccc
            position relative
            cursor pointer
            &:hover
                border 1px solid #00af9c
            &
                @media screen and (max-width: 900px)
                    width 32%
                @media screen and (max-width: 700px)
                    width 49%
                @media screen and (max-width: 500px)
                    width 100%
            img
                width 100%
            .itme-info
                width 100%
                height 170px
                background #ffffff
                position absolute
                top 158px
                box-sizing border-box
                padding 50px 20px 20px
                text-align left                 
                .goods-name
                    color #666666
                    font-size 14px
                .goods-price
                    color #f84c95
</style>


