<template>
  <div id="details">
    <div class="left-container">
      <img :src="detailsData.url" alt="">
    </div>
    <div class="goods-info">
      <h3 class="goods-title">{{detailsData.name}}</h3>
      <p class="goods-price">价格：<span class="goods-price-number"><span class="goods-price-symbol">￥</span>{{detailsData.price}}</span></p>
      <div class="add-goods-number">
        <span class="add-goods-number-text">数量：</span>
        <button class="add-goods-number-btn" @click="cutNumber()">-</button>
        <span class="add-goods-number-content">{{detailsNumber}}</span>
        <button class="add-goods-number-btn" @click="plusNumber()">+</button>
      </div>
      <div class="buy-goods">
        <button class="add-cart">加入购物车</button>
        <button class="go-buy">立即购买</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'details',
  data () {
    return {
      detailsData: {},
      detailsNumber: 1
    }
  },
  created () {
    this.getDetails(this.$route.path.split('/')[3])
  },
  watch: {
    '$route': function(route) {
      console.log(route)
    }
  },
  methods: {
    getDetails(path) {
      this.$http.get('./../../../../static/data/goodsDetails.json')
        .then(res => {
          this.detailsData = res.data[path]
          this.detailsNumber = res.data[path]['number']
        })
    },
    plusNumber() {
      this.detailsNumber += 1
    },
    cutNumber() {
      this.detailsNumber -= 1
    }
  }
}
</script>

<style lang="stylus" scoped>
  #details
    margin 100px auto
    width 90%
    display flex
    &
      @media screen and (max-width: 805px)
        flex-direction column
    .left-container
      flex 1
      &
        @media screen and (max-width: 805px)
          margin-bottom 30px
      img 
        width 100%
    .goods-info
      flex 1
      margin-top 40px
      &
        @media screen and (max-width: 1088px)
          margin-top 30px
        @media screen and (max-width: 1055px)
          margin-top 20px
        @media screen and (max-width: 1020px)
          margin-top  10px
        @media screen and (max-width: 980px)
          margin-top 0px
      .goods-price
        margin 40px auto
        &
          @media screen and (max-width: 1088px)
            margin 30px auto
          @media screen and (max-width: 910px)
            margin 10px auto
        .goods-price-number
          font-size 35px
          color #f84c3d
          font-weight 700
          .goods-price-symbol
            font-size 18px
            font-weight 500
      .add-goods-number
        margin-top 40px
        &
          @media screen and (max-width: 1088px)
            margin 30px auto
          @media screen and (max-width: 910px)
            margin 20px auto
        .add-goods-number-btn
          width 25px
          height 27px
          outline none
          background #ffffff
          border 1px solid #999
        .add-goods-number-content
          display inline-block
          width 50px
          height 25px
          line-height 25px
          box-sizing border-box
          position relative
          top 2px
          border 1px solid #999
      .buy-goods
        .add-cart, .go-buy
          margin-top 50px
          width 150px
          height 35px
          border none
          &:hover
            font-weight 600
        .add-cart
          border 1px solid #f84c3d
          background #ffffff
        .go-buy
          background #f84c3d
          color #ffffff
</style>



