<template>
    <div id="header">
        <ul class="header-list">
            <li class="header-itme" @click="scrollTo()">
                <router-link to="/home" tag="span">首页</router-link>
            </li>
            <li class="header-itme itme-none" @click="scrollTo()">
                <router-link to="/news" tag="span">家装新闻</router-link>
            </li>         
            <li class="header-itme itme-none" @click="scrollTo()">
                <router-link to="/ensure" tag="span">服务保障</router-link>
            </li>
            <span class="for-box" v-for="(list,index) of listArr" :key="index">
                <li class="header-itme" :class="{goodsClass:index === 0}" @click="scrollTo()">
                    <router-link :to="list.url" tag="span">{{list.name}}</router-link>
                <div class="show-block"></div>
                <ul class="show-list">
                    <span class="inner-for-box" v-for="(itme,index) of list.itmeArr" :key="index">
                        <li class="show-itme">{{itme.name}}</li>                        
                    </span>
                </ul>
            </li>
            </span>
            <li class="header-itme">全部商品</li>
            <i class="icon cart-icon material-icons" @click="pushRouter('/cart')">shopping_cart</i>
        </ul>
    </div>
</template>

<script>
export default {
    name:'header',
    data () {
        return {
            userPosition: '',
            listArr: []
        }
    },
    created () {
        this.getHeaderData()
    },
    methods: {
        pushRouter(push){
            this.$router.push(push)
            scrollTo(0,0)
        },
        scrollTo(){
            scrollTo(0,0)
        },
        getHeaderData(){
            this.$http.get('./../../../../static/data/headerData.json')
                .then(res => {
                    this.listArr = res.data
            })
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import "./../../../../static/stylus/variable.styl"
    #header
        user-select none            
        .header-list
            float left
            list-style none
            margin 40px 0 20px 80px
            .header-itme
                float left
                margin-right 40px
                border-bottom 2px solid #ffffff
                z-index 100
                .show-block
                    position absolute
                    z-index 1                
                    width 50px
                    height 100px
                .show-list
                    position absolute
                    z-index 100
                    text-align center
                    list-style none 
                    left 5%
                    margin 10px auto
                    box-sizing border-box
                    padding 30px                    
                    background #ffffff
                    width 90%
                    box-shadow 0 0 4px rgba(0,0,0,0.3)
                    display none
                    .inner-for-box
                        .show-itme
                            float left
                            margin 10px
                            box-sizing border-box                            
                            padding 10px 20px
                            &:hover 
                                cursor pointer
                                border-bottom 1px solid
                &:hover, &.router-link-active
                    border-bottom 2px solid $balck
                    cursor pointer
                    color $balck
                    ul
                        display block
            .goodsClass
                margin-left 50px
            .itme-none
                &
                    @media screen and (max-width: 1010px)
                        display none
            .cart-icon
                // margin-left 200px
                position absolute
                right 50px
                cursor pointer

</style>


