<template>
  <div class="home">
    <my-swiper></my-swiper>
    <div class="home-hot-title">
      <h2 class="home-hot">热门商品</h2>
      <p class="hot-sub-title">Hot commodity</p>
      <div class="hot-contenter">
        <div class="hot-shop-list" v-for="(itme, index) of imageArr" :key="index" :class="{'list-hidden': index !== 0}">
          <img :src="itme.imageUrl" alt=""> 
          <div class="hot-shop-text">
            <h4 class="hot-shop-name">{{itme.imageName}}</h4>
            <p class="hot-shop-info">￥{{itme.imageInfo}}</p>
            <div class="hot-shop-buy" @click="pushRouter(itme.buyUrl)">购买</div>
          </div>       
        </div>
        </div>
      </div>
      <div class="hot-shop-more">
        <p class="hot-shop-more-text">MORE <span class="right-arrow">></span></p>
      </div>
    </div>
  </div>
</template>

<script>
import MySwiper from './common/swiper/Index'
export default {
  name: "Home",
  data() {
    return {
      imageArr: [
        {
          imageUrl: './../../static/images/sf-1.jpg',
          imageName: '欧式真皮沙发套装',
          imageInfo: '26999',
          buyUrl: '/goods/living-room/欧式真皮沙发套装'
        },
        {
          imageUrl: './../../static/images/sf-2.jpg',
          imageName: '布艺沙发套装',
          imageInfo: '5999',
          buyUrl: '/goods/living-room/布艺沙发套装'          
        },
        {
          imageUrl: './../../static/images/sf-3.jpg',
          imageName: '单件布艺沙发',
          imageInfo: '999',
          buyUrl: '/goods/living-room/单件布艺沙发'          
        },
      ]
    }
  },
  components: {
    MySwiper
  },
  methods: {
    pushRouter(path){
      this.$router.push(path)
      scrollTo(0,0)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "./../../static/stylus/variable.styl"
  .home-hot
    margin 20px auto
    margin-top 50px
  .hot-sub-title
    font-size 12px
    margin-top -5px
  .hot-contenter
    width 90%
    height 700px
    background #3aacb0
    margin 10px auto
    display flex
    padding 50px 50px
    box-sizing border-box
  .hot-shop-list
    flex 1
    background palegreen
    height 500px
    margin-left 20px
    overflow hidden
    img 
      width 100%
  .hot-shop-list:first-child
    margin-left 0
  .hot-shop-text
    width 100%
    height 600px
    background #fff
    position relative
    top -5px
    box-sizing border-box
    padding 20px
    transition top .3s
    -webkit-transition top .3s
    &:hover
      top -50px
      cursor pointer
    .hot-shop-name
      font-size 18px
      background #fff
    .hot-shop-info
      box-sizing border-box
      padding-top 20px
      text-align center
      color #f84c95
      font-weight 600
      background #fff
    .hot-shop-buy
      width 80px
      margin 50px auto
      box-sizing border-box
      padding 5px 10px
      border 1px solid
      border-radius 3px
      &:hover
        color #000
  .hot-shop-more
    margin -70px auto 0
    color #ffffff
    width 90px
    height 30px
    border 1px solid #fff
    border-radius 3px
    .right-arrow
      display none
    .hot-shop-more-text
      cursor pointer
      line-height 30px
      &:hover
        .right-arrow
          display inline-block
  .list-hidden    
    &
      @media screen and (max-width: 850px)
          display none
  .hot-contenter
    &
      @media screen and (max-width: 850px)
        height 900px
      @media screen and (max-width: 700px)
        height 800px
      @media screen and (max-width: 600px)
        height 700px
      @media screen and (max-width: 500px)
        height 650px
      @media screen and (max-width: 450px)
        height 600px
  .hot-shop-list
    &
      @media screen and (max-width: 850px)
        height 700px
      @media screen and (max-width: 700px)
        height 600px
      @media screen and (max-width: 600px)
        height 500px
      @media screen and (max-width: 700px)
        height 450px
      @media screen and (max-width: 700px)
        height 400px
</style>




