<template>
  <div id="app">
    <my-header class="my-header" v-show="showHeader"></my-header>
    <menu-header class="menu-header"></menu-header>
    <fixed-header class="fixed-header" v-show="showFixedHeader"></fixed-header>
    <router-view class="router-view"/>
    <my-footer></my-footer>
  </div>
</template>

<script>
import MyHeader from "./components/common/header/Index";
import MenuHeader from "./components/common/header/MenuHeader";
import FixedHeader from "./components/common/header/FixedHeader"
import MyFooter from "./components/common/footer/Index";

export default {
  name: "app",
  data () {
    return {
      showHeader: true,
      showFixedHeader: false
    }
  },
  components: {
    MyHeader,
    MenuHeader,
    FixedHeader,
    MyFooter
  },
  methods: {
    scrollTop() {
      if(document.querySelector('body').clientWidth <=850){
        this.showHeader = false
        this.showFixedHeader =false
      }else{
        if(document.documentElement.scrollTop >= 180){
          this.showHeader = false
          this.showFixedHeader = true
        }else{       
          if(this.showFixedHeader && document.documentElement.scrollTop === 0){
            this.showHeader = true
            this.showFixedHeader = false
          }
        }
      }
    },
    onScroll() {
      window.onscroll = () => {
        this.scrollTop()  
      }
    }
  },
  created () {
    this.onScroll()
  }
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  user-select: none;
  margin-top: 60px;
}
.router-view {
  clear: both;
}
.router-link-active{
  border-bottom: 2px solid #00af9c;
  color: #00af9c;
}
.menu-header {
  display: none;
}
.fixed-header{
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0 0 4px rgba(0,0,0,.3);
  background: #ffffff;
  z-index: 1000;
}
@media screen and (max-width: 850px) {
  .my-header {
    display: none;
  }
  .fixed-header{
    display: none;
  }
  .menu-header {
    display: block;
    background: #fff;
    z-index: 1000;
  }
}
/* @media screen and (min-width: 850px) {
  .menu-header {
    display: none;
  }
  .my-header {
    display: block;
    background: #fff;
    z-index: 1000;
  }
} */
</style>



