import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import GoodsList from '@/components/common/goodsList/Index'
import GoodsDetails from '@/components/common/goodsDetails/Index'
import Cart from '@/components/Cart'
import News from '@/components/News'
import Ensure from '@/components/Ensure'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/goods/:name',
      name: 'GoodsList',
      component: GoodsList
    },
    {
      path: '/goods/:name/:id',
      name: 'GoodsDetails',
      component: GoodsDetails
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/news',
      name: 'News',
      component: News
    },
    {
      path: '/ensure',
      name: 'Ensure',
      component: Ensure
    }
  ]
})



