<template>
  <div class="swiper-container">
      <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(val,index) of banners" :key="index">
              <img :src="val.imgUrl">
          </div>
          <!-- <div class="swiper-slide">
              <img src="http://pic.linshimuye.com/image/-1/2017-11-4-45f0e2b70249-4544-b5a7-0c9c4b8f5d35.jpg" alt="">
          </div>
          <div class="swiper-slide">
              <img src="http://pic.linshimuye.com/image/-1/2017-11-4-45f0e2b70249-4544-b5a7-0c9c4b8f5d35.jpg" alt="">
          </div> -->
      </div>
      <div class="swiper-pagination"></div>
  </div>
</template>

<script>
export default {
  name: "Swiper",
  data() {
    return {
      banners: [
        { imgUrl: "http://www.nitorichina.com/assets/uploads/20170908/2017090812025319021.jpg" },
        { imgUrl: "http://www.nitorichina.com/assets/uploads/20170908/2017090812025319021.jpg" }
      ]
    };
  },
  methods: {
    initSwiper() {
      const mySwiper = new Swiper(".swiper-container", {
        loop: true,
        pagination: ".swiper-pagination",
        autoplay: 2000,
        autoplayDisableOnInteraction: false
      })
    }
  },
  mounted() {
    this.initSwiper()
  }
}
</script>

<style lang="stylus" scoped>
    .swiper-container
        width 90%
        // height 450px
    img 
        width 100%
</style>




