<template>
    <div id="header">
        <div class="header-top">
            <div class="header-logo">
                <img src="../../../assets/logo99.png" alt="" @click="pushRouter('/home')">
            </div>
            <div class="header-top-right">
                <i class="material-icons menu" @click="listShow()">menu</i>
            </div>
        </div>
        <div class="header-list" v-show="showList">
            <div class="search">
                  <input type="text" name="" id="" placeholder="搜索">
                 <i class="icon material-icons">search</i>
            </div>
            <ul class="header-list-list">
                <li class="header-itme" @click="scrollTo()">
                    <router-link to="/home" tag="span">首页</router-link>
                </li>
                <li class="header-itme" @click="scrollTo()">
                    <router-link to="/news" tag="span">家装新闻</router-link>
                </li>         
                <li class="header-itme" @click="scrollTo()">
                    <router-link to="/ensure" tag="span">服务保障</router-link>
                </li>
                <li class="header-itme" v-for="(itme,index) of listArr" :key="index" @click="pushRouter(itme.url)">{{itme.name}}</li>
                <li class="header-itme">全部商品</li>
            </ul>
            <div class="header-down">
                <div class="cart">
                    <i class="icon cart-icon material-icons"  @click="pushRouter('/cart')">shopping_cart</i>
                    <span class="text" @click="pushRouter('/cart')">购物车</span> 
                </div>
                <div class="user">
                    <span class="text login" @click="pushRouter('/login')">登录</span>
                    <span class="text register">注册</span>
                </div>        
            </div>
        </div>                   
    </div>
</template>

<script>
export default {
    name:'menu-header',
    data () {
        return {
            showList: false,
            listArr: []
        }
    },
    created () {
        this.getHeaderData()
    },
    methods: {
        pushRouter(push){
            this.$router.push(push)
            this.showList = false
            scrollTo(0,0)
        },
        scrollTo(){
            this.showList = false
            scrollTo(0,0)
        },
        listShow(){
            this.showList = !this.showList
        },
        getHeaderData(){
            this.$http.get('./../../../../static/data/headerData.json')
                .then(res => {
                    this.listArr = res.data
                })
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import "./../../../../static/stylus/variable.styl"
    #header
        width 100%
        background #ffffff
        position fixed
        top 0
        .header-top
            background #ffffff
            display flex
            width 100%
            height $header-hegiht
            box-shadow 0 0 4px rgba(0,0,0,.3)
            .header-logo
                width 90%
                img
                    float left
                    height w=80px
                    margin-left 20px
                    &
                        @media screen and (max-width: 480px)
                            height 50px
            .header-top-right
                background #ffffff       
                .menu
                    float right
                    cursor pointer
                    margin-top 30px
                    &
                        @media screen and (max-width: 480px)
                            margin-top 15px
        .header-list
            width 100%
            margin-top -20px
            margin-bottom 20px
            background #ffffff
            box-sizing border-box
            padding-bottom  30px
            border-bottom 1px solid rgba(0,0,0,0.25)
            .search
                input
                    margin 25px 0 0 0
                    outline none
                    width 300px
                    height 30px
                    border none
                    border-bottom 1px solid $balck
                .icon
                    font-size 30px
                    position relative
                    bottom -10px
                    cursor pointer
            .header-list-list
                margin-top 20px
                .header-itme
                    margin 5px
                    cursor pointer
                    box-sizing border-box
                    padding-bottom 10px
                    &:hover
                        color #000
            .cart, .user
                display inline-block
            .cart
                position relative
                left -100px
                cursor pointer                
                &:hover
                    color #000
            .user
                position relative
                right -100px
                .login, .register
                    cursor pointer
                    &:hover
                        color #000
            .text
                position relative
                bottom 5px
    // .router-link-active 
</style>


