<template>
  <div id="cart">
      <div class="cart-container">
          <div class="empty" v-show="cartList.length === 0">您的购物车为空</div>
          <div class="cart-list">
              <div class="cart-itme" v-for="(itme, index) of cartList" :key="index">
                  <img :src="itme.imgUrl" alt="">
                  <span class="center-text">
                      <span class="itme-title">{{itme.goodsName}} </span>
                      <span class="itme-price">￥{{itme.goodsPrice}} </span>
                      <span> 数量：{{itme.goodsNumber}} </span>
                  </span>
                  <div class="single-settlement">
                      <span class="remove-btn">删除</span>
                      <span class="single-settlement-btn">结算</span>
                  </div>
              </div>
          </div>
          <div class="settlement">
              <div class="settlmeent-btn">
                  <span>结算</span>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    name: 'cart',
    data () {
        return {
            cartList: []
        }
    },
    computed: {
      ...mapGetters[
          'getUserName'
      ]  
    },
    created () {
        this.getCartList()
    },
    methods: {
        getCartList() {
            this.$http.get('./../../../../static/data/cartDate.json')
                .then(res => {
                    console.log(res.data[this.$store.getters.getUserName])
                    this.cartList = res.data[this.$store.getters.getUserName]
                })
        }
    }
}
</script>

<style lang="stylus" scoped>
    #cart      
        user-select none
        .cart-container
            margin 10px auto
            padding-top 20px
            padding-bottom 20px
            position relative
            top 30px
            width 90%
            background #ffffff
            box-shadow 0 0 4px rgba(0,0,0,.3)
            border-radius 10px
            &
                @media screen and (max-width: 650px)
                    width 100%
                    box-shadow 0 0 0
                @media screen and (max-width: 480px)
                    margin-top -40px
            .empty
                line-height 420px
            .settlement
                margin 10px auto
                left 2.5%
                width 95%
                height 40px
                line-height 40px
                background #f8f8f8
                border 1px solid #e7e7e7
                border-radius 5px
                .settlmeent-btn
                    width 100px
                    height 100%
                    background #333333
                    float right 
                    cursor pointer
                    color #ffffff
            .cart-list
                .cart-itme
                    display flex
                    border 1px solid #e7e7e7
                    border-radius 3px
                    width 95%
                    height 70px
                    margin 20px auto
                    &
                        @media screen and (max-width: 473px)
                            height 60px
                        @media screen and (max-width: 365px)
                            height 50px
                    img 
                        width 100px
                        &
                            @media screen and (max-width: 473px)
                                width 90px
                            @media screen and (max-width: 365px)
                                width 75px
                    .center-text
                        line-height 70px
                        text-align center
                        margin 0 auto
                        .itme-price
                            color #f84c3d
                        &
                            @media screen and (max-width: 515px)
                                font-size 14px
                            @media screen and (max-width: 473px)
                                line-height 60px
                            @media screen and (max-width: 365px)
                                line-height 50px
                    .single-settlement
                        float right
                        .remove-btn, .single-settlement-btn
                            display inline-block
                            width 100px
                            height 30px
                            line-height 30px
                            position relative
                            top 20px               
                            margin-right 20px
                            font-size 14px
                            box-sizing border-box
                            &:hover
                                font-weight 600
                                cursor point
                            &
                                @media screen and (max-width: 700px)
                                    width 80px
                                    height 25px
                                    line-height 25px
                                    top 22.5px
                                    margin-right 15px
                                @media screen and (max-width: 473px)
                                    margin-right 10px
                                @media screen and (max-width: 424px)
                                    margin-right 5px
                                    width 50px
                                    top 20px
                                @media screen and (max-width: 365px)
                                    top 12px
                                    width 30px
                                    font-size 12px
                        .remove-btn
                            height 31px
                            border 1px solid  #f84c3d
                            top 21px
                            &
                                @media screen and (max-width: 700px)
                                    height 25px
                                    top 23.5px
                                @media screen and (max-width: 424px)
                                    top 21px
                                @media screen and (max-width: 365px)
                                    top 13px
                        .single-settlement-btn
                            background #f84c3d
                            color #ffffff
</style>



