<template>
    <div id="header">
        <div class="header-top">
            <div class="header-logo">
                <img src="./../../../assets/logo99.png" alt="" @click="pushRouter('/')">
            </div>
            <div class="header-top-main">
                <div class="welcome">
                    <span class="login">欢迎 <span class="login-name">{{getUserName}}</span> 光临风雨家居</span>
                </div>
            </div>
            <div class="header-top-right">
                <div class="search">
                    <input type="text" name="" id="" placeholder="搜索">
                    <i class="icon material-icons">search</i>
                </div>
                <div class="cart">
                    <i class="icon cart-icon material-icons" @click="pushRouter('/cart')">shopping_cart</i>     
                </div>
                <div class="user">
                    <span class="login" @click="pushRouter('/login')" v-show="getShowLog">登录</span>
                    <span class="register" v-show="getShowLog">注册</span>
                    <span class="logoff" @click="logOff()" v-show="getShowLog === false">注销</span>                    
                </div>     
            </div>
        </div>
        <div class="header-border">
            <div class="header-border-1"></div>
            <div class="header-border-2"></div>
            <div class="header-border-3"></div>
            <div class="header-border-4"></div>
            <div class="header-border-5"></div>
        </div>
        <ul class="header-list">
            <li class="header-itme" @click="scrollTo()">
                <router-link to="/home" tag="span">首页</router-link>
            </li>
            <li class="header-itme itme-none" @click="scrollTo()">
                <router-link to="/news" tag="span">家装新闻</router-link>
            </li>         
            <li class="header-itme itme-none" @click="scrollTo()">
                <router-link to="/ensure" tag="span">服务保障</router-link>
            </li>
            <span class="for-box" v-for="(list,index) of listArr" :key="index">
                <li class="header-itme" :class="{goodsClass:index === 0}" @click.stop="scrollTo()">
                    <router-link :to="list.url" tag="span">{{list.name}}</router-link>
                <div class="show-block"></div>
                <ul class="show-list">
                    <span class="inner-for-box" v-for="(itme,index) of list.itmeArr" :key="index">
                        <li class="show-itme" @click.stop="pushRouter('/')">{{itme.name}}</li>                        
                    </span>
                </ul>
            </li>
            </span>
            <li class="header-itme">全部商品</li>
        </ul>
        <div class="header-down-right">
            <div class="user-position">
                <i class="pin material-icons">pin_drop</i>
                <span id="position-info">您的位置： {{userPosition}}</span>
            </div>
            <div class="user-position-2">
                <i class="pin material-icons">pin_drop</i>
                <span id="position-info">{{userPosition}}</span>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import * as utils from './../../../utils/index'
export default {
    name:'header',
    computed: {
      ...mapGetters([
          'getUserName',
          'getShowLog'
      ])  
    },
    data () {
        return {
            userPosition: '',
            listArr: [],
            borderColor: 'red'
        }
    },
    created () {
        this.geoFindMe()
        this.getHeaderData()
        this.initUserInfo()
        this.showLog()
    },
    methods: {
        pushRouter(push){          
            this.$router.push(push)
            scrollTo(0,0)
        },
        scrollTo(){
            scrollTo(0,0)            
        },
        getHeaderData(){
            this.$http.get('./../../../../static/data/headerData.json')
                .then(res => {
                    this.listArr = res.data
                })
        },
        initUserInfo(){
           let userName = utils.getLocalStorage('userName')
           if(userName !== undefined && userName !== ''){
               this.$store.commit('setUserName', userName)
           }
        },
        showLog(){
            let userName = utils.getLocalStorage('userName')
            if(userName !== undefined && userName !== ''){
                this.$store.commit('setShowLog', false)
            }
        },
        logOff(){
            utils.setLocalStorage('userName', '')
            this.$store.commit('setUserName', '游客')
            this.$store.commit('setShowLog', true)            
        },
        geoFindMe(){
            if(!navigator.geolocation){
                this.userPosition = "您的浏览器不支持地理位置"
            }
            navigator.geolocation.getCurrentPosition(this.positionSuccess, this.positionError)
        },
        positionSuccess(position){
            let latitude = position.coords.latitude
            let longitude = position.coords.longitude
            let point = new BMap.Point(longitude, latitude)
            let geoc = new BMap.Geocoder()
            geoc.getLocation(point, rs => {  
                let addComp = rs.addressComponents
                this.userPosition = addComp.city === '' ? '无法获取您的位置' : addComp.city
            })
        },
        positionError(){
            this.userPosition = "无法获取您的位置"
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import "./../../../../static/stylus/variable.styl"
    #header
        user-select none
        .header-top
            display flex
            height $header-hegiht
            margin -40px 60px 0
            .header-logo
                width 200px
                img
                    width 100%
                    cursor pointer
                    margin-top -10px
            .header-top-main
                flex 1
                .welcome
                    line-height 70px
                    &
                        @media screen and (max-width: 1115px)
                            display none
                    .login-name
                        font-weight 600
            .header-top-right
                width 500px
                .search, .cart, .user
                    display inline-block
                .user
                    margin-left 20px
                    cursor pointer
                .user
                    span
                        &:hover
                            color #000
                input
                    margin 25px 0 0 0
                    outline none
                    width 260px
                    height 30px
                    border none
                    border-bottom 1px solid $balck
                .icon
                    font-size 30px
                    position relative
                    bottom -10px
                    cursor pointer
                .cart-icon
                    margin-left 20px
        .header-border
            width 100%
            height 1px
            display flex
            .header-border-1,.header-border-2,.header-border-3,.header-border-4,.header-border-5
                flex 1
                // margin-left 2px
                // margin-right 2px
            .header-border-1
                background  #95ABF5
            .header-border-2
                background  #63F5C8
            .header-border-3
                background  #B8BCA6
            .header-border-4
                background  #AF8FEF
            .header-border-5
                background  #F99CFF
        .header-list
            float left
            list-style none
            margin 40px 0 20px 80px
            .header-itme
                float left
                margin-right 40px
                background  #ffffff
                z-index 100
                // padding 
                .show-block
                    position absolute
                    z-index 1                
                    width 50px
                    height 10px
                .show-list
                    position absolute
                    z-index 100
                    text-align center
                    list-style none 
                    left 5%
                    margin 10px auto
                    box-sizing border-box
                    padding 30px                    
                    background #ffffff
                    width 90%
                    box-shadow 0 0 4px rgba(0,0,0,0.3)
                    display none
                    .inner-for-box
                        .show-itme
                            float left
                            margin 10px
                            box-sizing border-box                            
                            padding 10px 20px
                            &:hover 
                                cursor pointer
                                border-bottom 1px solid
                &:hover, &.router-link-active
                    border-bottom 2px solid  #00af9c
                    cursor pointer
                    color #00af9c
                    ul
                        display block
            .goodsClass
                margin-left 50px
            .itme-none
                &
                    @media screen and (max-width: 1010px)
                        display none
        .header-down-right
            margin-top 30px
            .user-position
                &
                    @media screen and (max-width: 1098px)
                        display none
                .pin
                    position relative
                    bottom -7px
            .user-position-2
                display none
                &
                    @media screen and (max-width: 1098px)
                        display block
                    @media screen and (max-width: 1070px)
                        display none
                .pin
                    position relative
                    bottom -7px

</style>


