<template>
  <div id="news">
      <div class="news-list">
          <div class="news-itme">
              <img src="./../../static/images/sf-3.jpg" alt="">
              <div class="news-itme-container">
                  <h3 class="news-itme-title">家装新闻</h3>
                  <p class="news-itme-text">家装新闻内容</p>
              </div>
          </div>
          <div class="news-itme">
              <img src="./../../static/images/sf-3.jpg" alt="">
              <div class="news-itme-container">
                  <h3 class="news-itme-title">家装新闻</h3>
                  <p class="news-itme-text">家装新闻内容</p>
              </div>
          </div>
      </div>
      <div class="news-hot-shop">
          <h3 class="news-hot-shop-title">热门商品</h3>
          <div class="news-hot-shop-container">
              <img src="./../../static/images/sf-2.jpg" alt="">
              <p class="news-hot-shop-name">热门商品名称1</p>
          </div>
          <div class="news-hot-shop-container">
              <img src="./../../static/images/sf-2.jpg" alt="">
              <p class="news-hot-shop-name">热门商品名称2</p>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  
}
</script>

<style lang="stylus" scoped>
    #news
        display flex
        width 90%
        margin 100px auto
        &
            @media screen and (max-width: 450px)
                width 100%
                margin 40px auto
    .news-list
        width 70%
        box-shadow 0 0 4px rgba(0,0,0,.3)
        padding 20px
        box-sizing border-box
        &
            @media screen and (max-width: 850px)
                width 100%
            @media screen and (max-width: 450px)
                width 100%
                box-shadow 0 0 0
        .news-itme
            display flex
            height 180px
            margin-bottom 40px
            &
                @media screen and (max-width: 850px)
                    height 150px
                @media screen and (max-width: 700px)
                    height 130px
                @media screen and (max-width: 600px)
                    height 110px
                @media screen and (max-width: 500px)
                    height 90px
                @media screen and (max-width: 450px)
                    height 80px
            img
                width 300px
                &
                    @media screen and (max-width: 850px)
                        width 250px
                    @media screen and (max-width: 700px)
                        width 230px
                    @media screen and (max-width: 600px)
                        width 200px
                    @media screen and (max-width: 500px)
                        width 170px
                    @media screen and (max-width: 450px)
                        width 150px
            .news-itme-container
                margin-left 28px
                text-align left
                .news-itme-title
                    &:hover
                        cursor pointer
                        color #00af8d
                    &
                        @media screen and (max-width: 700px)
                            font-size 16px
                .news-itme-text
                    color #666666
                    font-size 14px
                    margin-top 20px
    .news-hot-shop
        width 30%
        height 600px
        margin-left 20px
        box-shadow 0 0 4px rgba(0,0,0,.3)
        &
            @media screen and (max-width: 850px)
                display none
        .news-hot-shop-title
            color #f84c3d
            margin-top 20px
        .news-hot-shop-container
            cursor pointer
            width 100%
            padding 20px
            box-sizing border-box
            img
                width 100%
            .news-hot-shop-name
                color #666666
                font-size 14px
</style>



