import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  username: '游客',
  fixed: false,
  showLog: true,
  routePath: ''
}

const getters = {
  getUserName: state => state.username,
  getFixed: state => state.fixed,
  getShowLog: state => state.showLog,
  getRoutePath: state => state.routePath
}

const mutations = {
  setUserName(state, name) {
    state.username = name
  },
  setFixed(state, boolen) {
    state.fixed = boolen
  },
  setShowLog(state, boolen) {
    state.showLog = boolen
  },
  setRoutePath(state, path) {
    state.routePath = path
  }
}

const store = new Vuex.Store({
  state,
  getters,
  mutations
})

export default store



